
.fh-dashboard {
    // move the table padding to after the scrollable pane
    @include govuk-responsive-margin(6, "bottom");
}

[aria-sort] a,
[aria-sort] a:hover {
    background-color: transparent;
    border-width: 0;
    -webkit-box-shadow: 0 0 0 0;
    -moz-box-shadow: 0 0 0 0;
    box-shadow: 0 0 0 0;
    color: #005ea5;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    padding: 0 10px 0 0;
    position: relative;
    text-align: inherit;
    font-size: 1em;
    margin: 0;
    //todo: have mixin (and class) for fh-link-as-button (and fh-button-as-link)
    line-height: normal;
    text-decoration: none;
}

[aria-sort] a:focus {
    background-color: $govuk-focus-colour;
    color: $govuk-focus-text-colour;
    box-shadow: 0 -2px $govuk-focus-colour, 0 4px $govuk-focus-text-colour;
    outline: none;
}

[aria-sort]:first-child a {
    right: auto;
}

[aria-sort] a:before {
    content: " \25bc";
    position: absolute;
    right: -1px;
    top: 9px;
    font-size: 0.5em;
}

[aria-sort] a:after {
    content: " \25b2";
    position: absolute;
    right: -1px;
    top: 1px;
    font-size: 0.5em;
}

[aria-sort="ascending"] a:before,
[aria-sort="descending"] a:before {
    content: none;
}

[aria-sort="ascending"] a:after {
    content: " \25b2";
    font-size: .8em;
    position: absolute;
    right: -5px;
    top: 2px;
}

[aria-sort="descending"] a:after {
    content: " \25bc";
    font-size: .8em;
    position: absolute;
    right: -5px;
    top: 2px;
}
